<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>Password Update</h1>
        <hr>
				<form @submit.prevent="updateSettings();">
					<fieldset>
						<fieldset class="form-group">
							Current passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="currentPassword"
							placeholder="Password"
							/>
						</fieldset><br>
						<fieldset class="form-group">
							New passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="newPassword1"
							placeholder="Password"
							/>
						</fieldset><br>
						<fieldset class="form-group">
							Repeat new passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="newPassword2"
							placeholder="Password"
							/>
						</fieldset><br>
						<button class="btn btn-lg btn-primary pull-xs-right">
							Update Password
						</button>	
					</fieldset>
				</form>
        <br>
        <br>
        <h5>Related:</h5>
        <router-link :to="{ name: 'profileUpdate' }">
          <h3><i class="md-36 material-icons main">person</i> Profile update</h3>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import { UPDATE_PASSWORD, } from "@/store/actions.type";

import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";
  
export default {
	name: 'viewPasswordUpdates',

	data () {
		return {
			currentPassword: '',
			newPassword1: '',
			newPassword2: '',
		}
	},

  computed: {
  ...mapGetters(["currentUser"])
  },
	mounted() {
		// console.log(this.currentUser)
	},

	methods: {
		updateSettings () {


			
			if(this.currentPassword.length <= 4 || this.newPassword1.length <= 4 || this.newPassword2.length <= 4 ){
				this.$swal.fire({
					type: 'error',
					text: 'Empty password not allowed',
					showConfirmButton: false,
					showCloseButton: true,
				});
				return;
			}

			if(this.newPassword1 !== this.newPassword2){
				this.$swal.fire({
					type: 'error',
					text: 'New password must matched',
					showConfirmButton: false,
					showCloseButton: true,
				});
				return;
			}

			this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })

			this.$store.dispatch(UPDATE_PASSWORD, {
        password_old: this.currentPassword,
        password_1: this.newPassword1,
        password_2: this.newPassword2 
			}).then(value => {
        this.$swal.fire({
          type: 'success',
          text: 'Password updated',
          showConfirmButton: false,
          showCloseButton: true,
				})

				this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })

				this.currentPassword = "";
				this.newPassword1 = "";
				this.newPassword2 = "";
			
			}).catch(error => {
				console.log('error passwor update', error)
        this.$swal.fire({
          type: 'error',
          text: 'Unable to update password',
          showConfirmButton: false,
          showCloseButton: true,
        })

				this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })
			});


		},
	},
}
</script>

<style>

</style>



