<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1 id="checkOutBanner">Checkout</h1>
      </div>
    </div>
    <hr>
    <router-link :to="{ name: 'merchant', params: { merchantId: currentCart.merchant.friendlyId } }" v-if="currentCart.items.length > 0">
      <div class="row">
        <div class="col-xs-3">
          <img class="img-fluid" :src="currentCart.merchant.image" />
        </div>
        <div class="col-xs-9">
          <h3><b>{{ currentCart.merchant.name }}</b></h3>
          <h5 class="blackColor">{{ currentCart.merchant.address }}</h5>
        </div>
      </div>
    </router-link>
    <hr>
    <div class="row" v-if="currentCart.items.length > 0">
      <div class="col-xs-6">
        <b class="mainColor">
          Dine in / Take out
        </b>
        <select class="form-control highlightedForm" v-model.lazy="currentCart.collection.method" required>
          <option value="-1">-- Select --</option>
          <option value="dineIn" v-if="merchant.availableCollectionMethod == 1 || merchant.availableCollectionMethod == 2">Dine in</option>
          <option value="takeOut" v-if="merchant.availableCollectionMethod == 0 || merchant.availableCollectionMethod == 2">Take out</option>
        </select>
      </div>
      <transition name="component-fade" mode="out-in">
        <div class="col-xs-6" v-if="currentCart.collection.method == 'takeOut'">
          <b class="mainColor">I am coming in ...</b>
          <select class="form-control highlightedSubForm" v-model.lazy="currentCart.collection.timing" required>
            <option value="-1">-- X minutes --</option>
            <option value="0">Now</option>
            <option value="5">5 minutes</option>
            <option value="10">10 minutes</option>
            <option value="15">15 minutes</option>
            <option value="30">30 minutes</option>
          </select>
        </div>
        
        <div class="col-xs-6" v-if="currentCart.collection.method == 'dineIn'">
          <b class="mainColor">Table number</b>
          <input type="text" class="form-control highlightedSubForm" v-model="currentCart.collection.tableNumber" placeholder="#" required/>
        </div>
      </transition>
    </div>
    <hr v-if="currentCart.items.length > 0">
    <div class="row" v-if="currentCart.items.length > 0">
      <div class="col-xs-12">
        <h4>Items</h4>
          <table class="table table-sm">
            <thead>
              <tr>
                <th scope="col-9">Item</th>
                <th scope="col-3">Total</th>
              </tr>
            </thead>
            <tbody v-if="currentCart.items.length >= 1">
            <!-- <transition-group name="list" tag="tbody" v-if="currentCart.items.length >= 1"> -->
                <tr v-for="(item, idx) in currentCart.items" :key="idx" class="cartItem cellPadded">
                  <td>
                    <b @click="removeItem(idx)"><i class="ion-android-cancel text-warning"></i></b>&nbsp;&nbsp; <b>{{ item.name }}</b><br>
                    <span v-if="item.customisationApplied.length > 0">
                      <span class="itemCustomDetails">
                        <small><small>+ Base {{ item.name }} (SGD {{ item.price | 2decimal }})</small></small>
                      </span><br>
                      <span class="itemCustomDetails" v-for="(customisationItem, idx2) in item.customisationApplied" :key="idx2">
                        <small><small>
                          + {{ customisationItem.customiserCategoryName }}: {{ customisationItem.name }} (<span v-if="customisationItem.price == 0">Free</span><span v-if="customisationItem.price > 0">{{ customisationItem.price | currency }}</span>)
                        </small></small>
                        <br>
                      </span>
                    </span>
                  </td>
                  <td class="text-xs-right">SGD {{ (item.cartValue).toString() | 2decimal }}</td>
                </tr>
                <tr class="cartItem cellPadded">
                  <td colspan="2">
                    <span @click="addItem()" class="mainColor" v-if="currentCart.items.length > 0">
                      <i class="ion-plus"></i>&nbsp;&nbsp; <b>Add new item</b>
                    </span>
                  </td>
                </tr>
            <!-- </transition-group> -->
            </tbody>
            <tbody v-else>
              <tr>
                <td colspan="2">
                  No item selected
                </td>
              </tr>
            </tbody>
            <tfoot v-if="currentCart.items.length >= 1">
              <tr class="cellPadded">
                <td class="text-xs-right">Subtotal&nbsp;</td>
                <td class="text-xs-right">SGD {{ calcSubTotalStore | 2decimal }}</td>
              </tr>
              <tr class="cellPadded">
                <td class="text-xs-right mainColor">
                  <b v-if="currentCart.promotion" @click="removeVoucher()">
                    <i class="ion-android-cancel text-warning"></i>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                  </b>
                  <b>
                    <router-link :to="{ name: 'vouchers', params: { merchantId: this.merchant.friendlyId } }">
                      <b v-if="!currentCart.promotion">Select promotions</b>
                      <span v-if="currentCart.promotion">
                        Voucher {{ currentCart.promotion.name }}
                      </span>
                    </router-link>
                  </b>
                  &nbsp;
                  <span style="color:#777" v-if="!currentCart.promotion && currentCart.merchant.currentDiscount > 0">
                    ( Default: {{ parseFloat(currentCart.merchant.currentDiscount).toString() }}% )
                  </span>
                </td>
                <td class="text-xs-right">SGD {{ currentCart.discountValue | 2decimal }}</td>
              </tr>
              <tr class="cellPadded" v-if="parseFloat(currentCart.merchant.taxPercent) > 0">
                <td class="text-xs-right">Tax ( {{ parseFloat(currentCart.merchant.taxPercent).toString() }}% )&nbsp;</td>
                <td class="text-xs-right">SGD {{ currentCart.taxValue | 2decimal }}</td>
              </tr>
              <tr class="font-weight-bold cellPadded">
                <td class="text-xs-right">Total&nbsp;</td>
                <td class="text-xs-right">SGD {{ calcTotalStore | 2decimal }}</td>
              </tr>
            </tfoot>
          </table>
      </div>
    </div>
    <div class="row" v-if="currentCart.items.length >= 1">
      <div class="col-xs-12">
        <input class="form-control" type="text" placeholder="Notes to merchant"
          v-model="this.currentCart.notes"
        />
        <br>
      </div>
    </div>
    <div class="row" v-if="currentCart.items.length >= 1">
      <div class="col-xs-12 disclaimer">
        <br>
        <h5>Pay with</h5>
      </div>
    </div>
    <div class="row" v-if="currentCart.items.length >= 1">
      <div class="col-xs-5">
        <button type="button" class="btn btn-outline-primary btn-lg btn-block" @click="submitOrder('CASH')" v-ripple>
          CASH
        </button>
      </div>
      <div class="col-xs-7">
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="submitOrder('STASH')" v-ripple>
          STASH
        </button>
      </div>
    </div>
    <div class="row" v-if="currentCart.items.length <= 0">
      <div class="col-xs-12 text-xs-center">
        <br>
        <router-link :to="{ name: 'home', params: { merchantId: merchant.friendlyId } }">
          <h2>Continue browsing</h2>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { REMOVE_FROM_CART, NEW_CART, SET_VOUCHER, SET_COLLECTION_METHOD, } from "@/store/mutations.type";
import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

import OrdersService from '@/common/orders.service';

export default {
  name: "viewCart",
  data() {
    return {
      collectionMethod: '-1',
      collectionTime: '-1',
      tableNumber: '',
      promoCode: '',
      isPromoLoading: false,
      validPromo: false,
      // validPromo: {
      //   promoId: '',
      //   promoName: '',
      //   conditions: [
          
      //   ],
      //   value: {
      //     type: 'BOGO(Cheaper) / value / percentage',
      //     effect: 'cart / product',
      //     amount: '',
      //   },
      // },
    };
  },
  computed: {
    ...mapGetters([
                   "isAuthenticated",
                   "currentCart", "merchant",
                   "calcSubTotalStore", "calcDiscountStore", "calcTaxStore", "calcTotalStore"
                  ])
  },
  methods: {
    scrollToTop() {
      // window.scrollTo(0,0);
      // this.$router.push({ name: 'cart', hash: '#checkOutBanner', })
    },

    gotoPromoPage () {
      // alert("No promo found")
      this.$swal.fire({
        type: 'info',
        text: 'No promo found',
        showConfirmButton: false,
        showCloseButton: true,
      })
    },

		// calcItemValue(item){
    //   let result = parseFloat(item.price)
    //   // console.log(item)
    //   for(let i = 0; i < item.customisationApplied.length; i++){
    //     result += parseFloat(item.customisationApplied[i].price)
    //   }
    //   return result;
		// },

		// calcSubTotal(){
    //   let result = 0
    //   console.log('currentCart', this.currentCart, this.currentCart.items.length, 'items')
    //   console.log(result, 0)
    //   for(let i = 0; i < this.currentCart.items.length; i++){
    //     console.log(result, this.calcItemValue(this.currentCart.items[i]))
    //     result += parseFloat(this.calcItemValue(this.currentCart.items[i]))
    //   }
    //   console.log(result, 0)
    //   return result;
    // },
    
    // calcTotal() {
    //   return this.calcSubTotal();
    // },

    addItem() {
      console.log("addItem clicked|"+this.currentCart.merchant.friendlyId+"|")
          
      this.$ga.event('cart', 'items', 'add_item', true);

      // this.$router.push({ name: 'merchant', params: { merchantId: this.currentCart.merchant.friendlyId }})

      this.$router.push({ name: 'merchant', params: { merchantId: this.currentCart.merchant.friendlyId }})

    },

    removeItem(idx) {
      let item = this.currentCart.items[idx]
          
      this.$ga.event('cart', 'items', 'remove_item', 'attempt');

      this.$swal({
        title: 'Are you sure?',
        // text: 'Removing ' + item.name + ' from your cart?',
        html: 'Removing <b>' + item.name + '</b> from your cart?',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes Delete it!',
        cancelButtonText: 'No, Keep it!',
        showCloseButton: true,
        showLoaderOnConfirm: true
      }).then((result) => {
        if(result.value) {
          // this.$swal('Deleted', 'You successfully deleted this file', 'success')
          
          this.$ga.event('cart', 'items', 'remove_item', true);
        
          this.$store.commit(REMOVE_FROM_CART, idx)
        } else {
          
          this.$ga.event('cart', 'items', 'remove_item', false);

          // this.$swal('Cancelled', 'Your file is still intact', 'info')
          return 0;
        }
      })

      // return 0;
      
      // if (window.confirm('Proceed removing ' + item.name + ' from your cart?')) {
      //   this.$store.commit(REMOVE_FROM_CART, idx)
      // }else{
      //   return 0;
      // }

      if(this.currentCart.items.length <= 0){
          
        this.$ga.event('cart', 'items', 'remove_item', 'no_more_item');

        this.$router.push({ name: 'merchant', params: { merchantId: this.currentCart.merchant.id }})
      }
    },

    // calcTotalItemPrice (item) {
    //   return 0;
    // },
    
    removeVoucher() {
          
      this.$ga.event('cart', 'voucher', 'remove_voucher', 'attempt');

      this.$swal({
        title: 'Are you sure?',
        html: 'Removing voucher <b>' + this.currentCart.promotion.name + '</b>?',
        type: 'warning',
        // showCancelButton: true,
        confirmButtonText: 'Yes Delete it!',
        cancelButtonText: 'No, Keep it!',
        showCloseButton: true,
        showLoaderOnConfirm: true
      }).then((result) => {
        if(result.value) {
          
        this.$ga.event('cart', 'voucher', 'remove_voucher', true);

          // this.$swal('Deleted', 'You successfully deleted this file', 'success')
          this.$store.commit(SET_VOUCHER, null)
        } else {
          
          this.$ga.event('cart', 'voucher', 'remove_voucher', false);
          // this.$swal('Cancelled', 'Your file is still intact', 'info')
          return 0;
        }
      })


      // if (window.confirm('Proceed removing voucher ' + this.currentCart.promotion.name + '?')) {
      //   this.$store.commit(SET_VOUCHER, null)
      // }else{
      //   return 0;
      // }
    }, 

    submitOrder (paymentType) {

      if(!this.isAuthenticated){
        
        this.$ga.event('cart', 'submit', 'no_login_attempt', true);
          
        this.$swal({
          title: 'Login to place order',
          type: 'warning',
          confirmButtonText: 'Login',
          // showCloseButton: true,
          showLoaderOnConfirm: true
        }).then((result) => {
          if(result.value) {
            
            this.$router.push({ name: 'login', })
            
          } else {
            // this.$swal('Cancelled', 'Your file is still intact', 'info')
        
            this.$ga.event('cart', 'submit', 'no_login_rejet', true);

            return 0;
          }
        })

        return
      }

      if(paymentType !== 'CASH' && paymentType !== 'STASH'){
          
        this.$ga.event('cart', 'setup', 'error', 'INVALID_PAYMENT_METHOD');

        // https://github.com/caiofsouza/vue-backtotop/blob/master/src/BackToTop.vue
        // let currentScroll = document.documentElement.scrollTop || document.body.scrollTop
        // if (currentScroll > 0) {
        //   window.requestAnimationFrame(window.smoothscroll)
        //   window.scrollTo(0, Math.floor(currentScroll - (currentScroll / 5)))
        // }

        this.$swal.fire({
          type: 'warning',
          text: 'Invalid payment method',
          showConfirmButton: false,
          showCloseButton: true,
        }).then((result) => {

          this.scrollToTop()

        })


        return
      }

      if(this.currentCart.items.length == 0){
          
        this.$ga.event('cart', 'setup', 'error', 'NO_ITEM_IN_CART');

        this.$swal.fire({
          type: 'warning',
          text: 'No item selected',
          showConfirmButton: false,
          showCloseButton: true,
        })
        return
      }

      if(this.currentCart.collection.method == 'eatIn'){
        if(this.currentCart.collection.tableNumber.length < 1){
          // alert('')
          
          this.$ga.event('cart', 'setup', 'error', 'NO_TABLE_NUMBER');

          this.$swal.fire({
            type: 'warning',
            text: 'Please provide valid table number',
            showConfirmButton: false,
            showCloseButton: true,
          }).then((result) => {

          this.scrollToTop()
          
        })

          return
        }else{
          // Proceed
        }
      }else if(this.currentCart.collection.method == 'takeOut'){
        if([0, 5, 10, 15, 30].includes(parseInt(this.currentCart.collection.timing))){
          // Proceed
        }else{
          // alert('')
          
          this.$ga.event('cart', 'setup', 'error', 'NO_PICK_UP_TIME');

          this.$swal.fire({
            type: 'warning',
            text: 'Please select pick-up time',
            showConfirmButton: false,
            showCloseButton: true,
          }).then((result) => {

          this.scrollToTop()
          
        })

          return
        }
      }else{
        // alert('')
          
        this.$ga.event('cart', 'setup', 'error', 'NO_COLLECTION_METHOD');

        this.$swal.fire({
          type: 'warning',
          text: 'Please select collection method',
          showConfirmButton: false,
          showCloseButton: true,
        }).then((result) => {

          this.scrollToTop()
          
        })

        return
      }

      if(this.currentCart.collection.tableNumber.length >= 1 && this.currentCart.collection.timing !== '-1'){
        // alert('Please select dine in or take-out')
          
        this.$ga.event('cart', 'setup', 'error', 'NO_COLLECTION_METHOD_ERROR');

        this.$swal.fire({
          type: 'warning',
          text: 'Please select dine-in or take-out',
          showConfirmButton: false,
          showCloseButton: true,
        }).then((result) => {

          this.scrollToTop()
          
        })

      }
      
      let oInfo = {
        menu: [
          // {
          //   uniqueId: '',
          //   menuId: '',
          //   qty: '',
          //   menuPrice: '',
          //   variation: '',
          //   totalVariationPrice: '',
          //   totalPrice: '',

          //    name: '',
          // },
        ],
        promotion: { },
        itemCount: this.currentCart.items.length,
        subtotal: parseFloat(this.calcSubTotalStore).toFixed(2),
        discount: parseFloat(this.calcDiscountStore).toFixed(2),
        tax: parseFloat(this.calcTaxStore).toFixed(2),
        total: parseFloat(this.calcTotalStore).toFixed(2),
      }
      
      oInfo.promotion = {
        promoId: '',
        promoCode: '',
        promoName: '',
      }
      
      oInfo.discount = 0
      
      oInfo.tax = 0

      for(let i = 0; i < this.currentCart.items.length; i++){
        let tmpVariation = ''
        let tmpVariationPrice = 0

        console.log('tmp', this.currentCart.items[i].customisationApplied)

        for(let x = 0; x < this.currentCart.items[i].customisationApplied.length; x++){
          tmpVariation = tmpVariation + this.currentCart.items[i].customisationApplied[x].customiserCategoryName + '|'
          tmpVariation = tmpVariation + this.currentCart.items[i].customisationApplied[x].name + '|'
          tmpVariation = tmpVariation + this.currentCart.items[i].customisationApplied[x].price + '\n'
          tmpVariationPrice = tmpVariationPrice + this.currentCart.items[i].customisationApplied[x].price
        }

        let tmpItem = {
          uniqueId: 0,
          menuId: this.currentCart.items[i].id,
          name: this.currentCart.items[i].name,
          qty: 1,
          menuPrice: this.currentCart.items[i].price,
          variation: tmpVariation,
          totalVariationPrice: tmpVariationPrice,
          totalPrice: this.currentCart.items[i].price + tmpVariationPrice,
        }

        oInfo.menu.push( tmpItem )
      }



      // console.log(this.currentCart)

      let orderObject = {
        merchant_id: this.currentCart.merchant.id,
        payment_type: paymentType,
        table_no: this.currentCart.collection.tableNumber,
        delay_by: this.currentCart.collection.timing,
        amount: parseFloat(this.calcTotalStore).toFixed(2),
        oinfo: JSON.stringify(oInfo),
      }


      console.log(orderObject)

      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });

      OrdersService.create(orderObject).then(data => {
        console.log('Order success', data.data.sales_id)
        
        this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });

        this.$store.commit(NEW_CART, { root: true });

        this.$router.push({ name: "orderDetails", params: { 'orderId': data.data.sales_id } })

      }).catch(error => {
        
        this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });
        
        console.log('Order not created|', error.response.status, "|", error, "|", error.response)

        if(error.response.status != 400){
          // alert('')
          
          this.$swal.fire({
            type: 'error',
            text: 'Unable to create order, please try again in a few minutes',
            showConfirmButton: false,
            showCloseButton: true,
          })
          return
        }
        
        if(error.response.data.status == 39){
          // alert('')
          
          // this.$swal.fire({
          //   type: 'error',
          //   text: 'Insufficient balance, please topup your wallet',
          //   confirmButtonText: 'Topup now'
          // })
          // .then(function(isConfirm) {
          //   if (isConfirm) {
          //     this.$swal.fire({
          //       title: 'Shortlisted!',
          //       text: 'Candidates are successfully shortlisted!',
          //       icon: 'success'
          //     }).then(function() {
          //       // form.submit(); // <--- submit form programmatically
          //       alert("Submit")
          //     });
          //   } else {
          //     this.$swal.fire("Cancelled", "Your imaginary file is safe :)", "error");
          //   }
          // })
          
          this.$ga.event('cart', 'payment', 'balance_sufficiency', false);
          
          this.$swal({
            title: 'Insufficient balance',
            html: 'Please topup your wallet?',
            type: 'error',
            confirmButtonText: 'Topup now',
            // showCloseButton: true,
            showLoaderOnConfirm: true
          }).then((result) => {
            if(result.value) {
              
              this.$router.push({ name: 'walletTopUp', })
              
            } else {
              // this.$swal('Cancelled', 'Your file is still intact', 'info')
          
              this.$ga.event('cart', 'payment', 'top_up_question', false);

              return 0;
            }
          })

          return

        }else{
          // alert('Unable to create order, please try again in a few minutes')
          
          this.$swal.fire({
            type: 'error',
            text: 'Please update your STASH, or contact hello@stash.asia',
            showConfirmButton: false,
            showCloseButton: true,
          })
        }

      })


    }
  }
};
</script>

<style scoped>
.img-fluid{
  border-radius: 15px;
  width: 150px;
  height: 150x;
}


.cartItem{
  font-size: 125%
}
tr.cellPadded > td  {
  padding: 15px 0; 
}
.mainColor{
  color: #ffba00;
}
.highlightedForm{
  border: 1px solid #ffba00;
  font-weight: bold;
  color: #2f3155;
  background-color: #ffba00;
}
.highlightedSubForm{
  border: 1px solid #ffba00;
  font-weight: bold;
  color: #2f3155;
}
.itemCustomDetails{
  padding-left: 38px;
}

.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
}
.component-fade-enter, .component-fade-leave-to
/* .component-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}


.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}

</style>


