<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
				<br>
				<h1>Reset your password</h1>
				<hr>
			</div>
		</div>
		<div class="row">
			<div class="col-xs-12">
				<p>
					Please enter your email address to search for your account.
				</p>
				<form>
					<fieldset>	
						<fieldset class="form-group">
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="email"
							placeholder="Email"
							/>
						</fieldset>
					</fieldset>
				</form>
				<br>
			</div>
		</div>
		<div class="row">
			<div class="col-xs-6">
				<router-link :to="{ name: 'login', params: {} }" class="btn btn-lg btn-outline-primary btn-block" >
					Cancel
				</router-link>
			</div>
			<div class="col-xs-6">
				<button class="btn btn-lg btn-primary btn-block" @click="forgetPasswordSendRequest()">
					Send reset email
				</button>	
			</div>
		</div>
	</div>
</template>

<script>
	import { FORGET_PASSWORD_REQUEST } from "@/store/actions.type";
	import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

	export default {
		name: "viewForgetPasswordRequest",
		data () {
			return {
				email: '',
			}
		},

		methods: {
			forgetPasswordSendRequest () {
				
					this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })
					
					this.$store.dispatch(FORGET_PASSWORD_REQUEST, this.email).then(response => {
						
						this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })
						
						this.$swal.fire({
							type: 'warning',
							title: 'Forget Password Request',
							html: 'Email with temporary password has been sent to <b>'+this.email+'</b>',
							showConfirmButton: false,
							showCloseButton: true,
						})

						this.$router.push({ name: 'login' })

					}).catch(error => {
						
						this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true })
						
						console.log('erro123r', error);
						this.$swal.fire({
							type: 'warning',
							text: 'Unable to reach server, please try again in a few minutes',
							showConfirmButton: false,
							showCloseButton: true,
						})
					})


			}
		}
	};
</script>



