<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
				<br>
				<h1>Profile update</h1><hr>
				<form>
					<fieldset>	
						<fieldset class="form-group">
							Name
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="currentUser.username"
							@click="contactStashAsia"
							placeholder="Your username"
							disabled
							/>
						</fieldset><br>
						<fieldset class="form-group">
							Email
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="currentUser.email"
							placeholder="Email"
							@click="contactStashAsia"
							disabled
							/>
						</fieldset><br>
					</fieldset>
				</form>
				<br>
        <br>
        <h5>Related:</h5>
        <router-link :to="{ name: 'passwordUpdate' }">
          <h3><i class="md-36 material-icons main">security</i> Password update</h3>
        </router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from "vuex";
	import { CHECK_AUTH } from "@/store/actions.type";

	export default {
	  name: "viewProfileUpdates",
	  computed: {
		...mapGetters(["currentUser"])
		},
		created() {
			this.$store.dispatch(CHECK_AUTH, true);
		},
	  methods: {
			contactStashAsia() {
				// alert("")
					
				this.$swal.fire({
					type: 'info',
					text: 'Please contact our hello@stash.asia',
					showConfirmButton: false,
					showCloseButton: true,
				})
			},
			
	  }
	};
</script>



