<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
        <br>
        <router-link :to="{ name: 'cart', }">
          <b><b class="ion-android-arrow-back mainColor"></b> Cart</b>
        </router-link>
			</div>
		</div>
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h4>{{ merchant.name }} / {{ merchant.address }}</h4>
        <h1>Vouchers</h1>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h4 v-if="merchant.vouchers.length <= 0">No active vouchers found</h4>
        <br v-if="merchant.vouchers.length <= 0">
        <br v-if="merchant.vouchers.length <= 0">
        <button v-if="merchant.vouchers.length <= 0" class="btn btn-primary btn-lg btn-block" @click="gotoCart()">
          Back to Cart
        </button>
        <div v-if="merchant.vouchers.length > 0">
          <div class="voucherCard" v-for="(voucher, idx) in merchant.vouchers" :key="idx" @click="applyVoucher(voucher)">
            <h5><b>{{ voucher.name }}</b></h5>
            <p>
              <small>
                Expired at: Monday, Octorber 5th, 2019 (feature Not implemented)
              </small>
            </p>
            {{ voucher.description }}
          </div>
        </div>
      </div>
    </div>
	</div>
</template>

<script>

import { mapGetters } from "vuex";

import { SET_VOUCHER, } from "@/store/mutations.type";

export default {
  name: "viewVouchers",
  computed: {
    ...mapGetters(["merchant"]),
  },
  components: { },
  data() {
	  return { }
  },
  methods: {

    gotoCart() {
      this.$router.push({ name: 'cart', })
    },

    applyVoucher(voucherObj) {

      this.$swal({
        html: 'Applying voucher <b>' + voucherObj.name + '</b>?',
        type: 'info',
        showCancelButton: true,
        confirmButtonText: 'Yes, Apply',
        cancelButtonText: 'No',
        showCloseButton: true,
        showLoaderOnConfirm: true
      }).then((result) => {
        if(result.value) {
          // alert('Voucher applied')
          console.log("Voucher clicked", voucherObj)
          try {
            this.$store.commit(SET_VOUCHER, voucherObj)
          
          }catch(err) {
            this.$store.commit(SET_VOUCHER)

            switch(err){
              case "NOT_ENOUGH_MIN":
                // alert("")
            
                this.$swal.fire({
                  type: 'error',
                  text: 'Not enough cart value to apply this voucher',
                  showConfirmButton: false,
                  showCloseButton: true,
                })

                break

              case "NOT_ENOUGH_QTY":
                // alert("")
            
                this.$swal.fire({
                  type: 'error',
                  text: 'Please select another item to apply this voucher',
                  showConfirmButton: false,
                  showCloseButton: true,
                })
                break

              case "INVALID_VOUCHER":
                // alert("")
                
                this.$swal.fire({
                  type: 'error',
                  text: 'Invalid voucher, please contact this merchant',
                  showConfirmButton: false,
                  showCloseButton: true,
                })
                break

              default:
                throw err
            }
            return
          }
          this.$router.push({ name: 'cart', })
        } else {
          // this.$swal('Cancelled', 'Your file is still intact', 'info')
          return 0;
        }
      })


      // if (window.confirm('Proceed applying voucher ' + voucherObj.name + ' for your cart?')) {
      //   // alert('Voucher applied')
      //   console.log("Voucher clicked", voucherObj)
      //   try {
      //     this.$store.commit(SET_VOUCHER, voucherObj)
        
      //   }catch(err) {
      //     this.$store.commit(SET_VOUCHER)

      //     switch(err){
      //       case "NOT_ENOUGH_MIN":
      //         // alert("")
					
      //         this.$swal.fire({
      //           type: 'error',
      //           text: 'Not enough cart value to apply this voucher',
      //           showConfirmButton: false,
      //           showCloseButton: true,
      //         })

      //         break

      //       case "NOT_ENOUGH_QTY":
      //         // alert("")
					
      //         this.$swal.fire({
      //           type: 'error',
      //           text: 'Please select another item to apply this voucher',
      //           showConfirmButton: false,
      //           showCloseButton: true,
      //         })
      //         break

      //       case "INVALID_VOUCHER":
      //         // alert("")
              
      //         this.$swal.fire({
      //           type: 'error',
      //           text: 'Invalid voucher, please contact this merchant',
      //           showConfirmButton: false,
      //           showCloseButton: true,
      //         })
      //         break

      //       default:
      //         throw err
      //     }
      //     return
      //   }
      //   this.$router.push({ name: 'cart', })
      // }else{
      //   return 0;
      // }
    }
  }
}
</script>

<style scoped>
  .voucherCard{
    border: 1px solid #ffba00;
    overflow: hidden;
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
  }
  .voucherCard > div > div > h5{
    color: #ffba00;
  }
  .voucherCard > div > div > .img-fluid{
    border-radius: 15px;
  }
  .voucherCard > div > div > p{
    color: #898476 ;
  }
</style>


