<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <h4>Hi <span class="text-uppercase font-weight-bold">{{ currentUser.username }}</span>! <small> Your STASH Cash balance:</small></h4>
        <br>
        <div class="text-xs-center">
          <h1 class="text-xs-center font-weight-bold" v-if="isShowBalance">{{ currentUser.wallet.ccy }} {{ currentUser.wallet.value | 2decimal }}</h1>
          <h1 class="text-xs-center font-weight-bold" v-if="!isShowBalance">{{ currentUser.wallet.ccy }} ###.##</h1>
          <button class="btn btn-primary btn-xl" @click="gotoTopUpWallet">
            <i class="ion-ios-plus-outline"></i><span>&nbsp;Top Up Balance</span>
          </button>
        </div>   
        <br>     
        <br>   
      </div>
    </div>
    <div class="row">
      <!-- <div class="col-xs-4 text-xs-center">
          <button class="btn btn-outline-primary btn-xl btn-block" @click="gotoLastTransactions">
            <i class="ion-bag"></i><span></span>
          </button>      
      </div> -->
      <!-- <div class="col-xs-6 text-xs-center">
          <button class="btn btn-outline-primary btn-md btn-block" @click="gotoInbox">
            <h3><i class="ion-android-mail"></i><span></span></h3>
          </button>      
      </div> -->
      <div class="col-xs-6 text-xs-center">
          <button class="btn btn-outline-primary btn-md btn-block" @click="openMyQR">
            <h3><i class="ion-qr-scanner"></i><span></span>&nbsp;&nbsp;My QR</h3>
          </button>      
      </div>
      <div class="col-xs-6 text-xs-center">
          <button class="btn btn-outline-primary btn-md btn-block" @click="gotoSettings">
            <h3><i class="ion-gear-b"></i><span></span>&nbsp;&nbsp;Settings</h3>
          </button>      
      </div>
      <!-- <div class="col-xs-12 text-xs-center">
          <button class="btn btn-outline-primary btn-md btn-block" @click="gotoSettings">
            <h3><i class="ion-gear-b"></i><span>&nbsp;&nbsp;Settings</span></h3>
          </button>      
      </div> -->
    </div>
    <div class="row">
      <div class="col-xs-12">
        <br>
        <p><h3><b>Latest Transactions</b></h3></p>
          <table class="table">
            <thead>
              <tr>
                <th scope="col">Merchant</th>
                <th scope="col">Amount</th>
                <th scope="col">Date Time</th>
              </tr>
            </thead>
            <tbody v-if="isShowBalance">
              <tr v-for="(transaction, idx) in currentUser.lastTransaction" :key="idx">
                <td @click="gotoOrderDetails(transaction.id)">
                  <span v-bind:style="{ color: statusToColor(transaction.status), 'font-weight': isBold(transaction.status) }">
                    <span class="currentOrder" v-if="transaction.status === 'WAITING'"><small>Current Order</small></span>
                    <br v-if="transaction.status === 'WAITING'">
                    <!-- <i class="ion-arrow-right-b"></i> &nbsp; -->
                    {{ transaction.merchant }}
                  </span>
                </td>
                <td @click="gotoOrderDetails(transaction.id)">SGD {{ transaction.value | 2decimal }}</td>
                <!-- <td v-if="!isShowBalance">SGD ###.##</td> -->
                <td @click="gotoOrderDetails(transaction.id)">{{ transaction.dateTime | formatDateWallet }}</td>
              </tr>
            </tbody>
            <tbody v-if="!isShowBalance">
              <tr v-for="(transaction, idx) in currentUser.lastTransaction" :key="idx">
                <td>
                  ############
                </td>
                <td>###.##</td>
                <!-- <td v-if="!isShowBalance">SGD ###.##</td> -->
                <td>#########</td>
              </tr>
            </tbody>
          </table>
        <hr>
      </div>
    </div>
    <!-- <div class="row">
      <div class="col-xs-12">
        <div class="text-xs-center">
          <br>
          <router-link
            class="btn btn-lg btn-block"
            exact
            :to="{ name: 'settings' }"
          >
            <button class="btn btn-primary btn-xl">
              <i class="ion-gear-a"></i>&nbsp;Settings
            </button>
          </router-link>
        </div>
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { CHECK_AUTH } from "@/store/actions.type";

export default {
  name: "viewWallet",
  data () {
    return {
      isShowBalance: true,
    }
  },
  computed: {
    ...mapGetters(["currentUser"])
  },
  methods: {
    statusToColor (status = '') {
      // console.log('statusToColor', status)
      switch(status){
        case 'SUCCESS':
        case 'WAITING':
          return '#ffba00'
        case 'FAILED':
          return '#FF7F7F'
        default:
          return '#000'
      }
    },

    isBold (status = '') {
      // console.log('isBold', status)
      switch(status){
        case 'WAITING':
          return 'bold'
        default:
          return 'normal'
      }
    },

    openMyQR () {

      let qrBase = "https://chart.googleapis.com/chart?cht=qr&chs=200x200&chld=H|1&choe=UTF-8&chl="

      // console.log(this.currentUser.id)

      let myQR = (qrBase+this.currentUser.id);

      this.isShowBalance = false;      

      this.$swal.fire({
        title: '<h2 style="color:#ffba00"><b>STASH ID QR</b></h2>',
        html: "<img src='"+myQR+"' /><h3><b>"+this.currentUser.id+"</b></h3><h2>"+this.currentUser.username+"</h2>",
        showConfirmButton: false,
        showCloseButton: true,
      }).then((result) => {
        this.isShowBalance = true;
      })

      // this.isShowBalance = true;

    },

    gotoTopUpWallet () {
      this.$router.push({ name: "walletTopUp"})
    },

    gotoOrderDetails (orderId) {
      this.$router.push({ name: "orderDetails", params: { orderId: orderId }})
    },

    gotoLastTransactions () {
      // alert('Feature work in progress')

      this.$swal.fire({
        type: 'info',
        html: 'Feature work in progress',
        showConfirmButton: false,
        showCloseButton: true,
      })

      // this.$router.push({ name: "inbox", })
    },

    gotoInbox () {
      this.$router.push({ name: "inbox", })
    },

    gotoSettings () {
      this.$router.push({ name: "settings", })
    },
  },

  created() {

    // console.log("created here")

    this.$store.dispatch(CHECK_AUTH, true).catch(error => {

      console.log("Error here when checking auth full load :(", error)

      this.$router.push({ name: 'login', })

      this.$swal.fire({
        type: 'info',
        html: 'You are logged out',
        showConfirmButton: false,
        showCloseButton: true,
      })
    });
  }
};
</script>

<style scoped>
/* table.table{
  table-layout: fixed;
} */
.successTx{
  color: #ffba00;
}
.processingTx{
  color: #555;
}
.failedTx{
  color: #ff0000;
}
.currentOrder{
  background-color: green;
  color: white;
  padding: 1px 3px;
}
</style>


