<template>
	<div>
    <div v-on:click="toggleCategory()" class="menuCategory" v-ripple>
      <h4 class="text-uppercase" v-bind:class="{ mainColor: menuCategory.isSelected }"><i v-if="menuCategory.isSelected" class="ion-arrow-down-b"></i><i v-if="!menuCategory.isSelected" class="ion-arrow-right-b"></i>&nbsp;&nbsp;{{ menuCategory.name }}</h4>
    </div>
    
    <!-- <transition-group name="list" tag="div"> -->
      
      <div class="menuList" v-if="menuCategory.isSelected">

        <div class="item" v-for="(item, idx) in menuCategory.menuList" :key="idx" @click="checkItem(idx)" v-ripple>
          <div class="row itemContent">
            <div class="col-xs-3">
              <div class="itemImage img-fluid">
                <div v-if="item.availability === '0'" class="notOpOverlay">
                  <div class="text">Not available</div>
                </div>
                <img class="img-fluid" :src="item.image" />
              </div>
            </div>
            <div class="col-xs-6 text-uppercase itemName">
              <div class="font-weight-bold" v-bind:class="{ notAvailableColor: item.availability === '0' }">{{ item.name }}</div>
            </div>
            <div class="col-xs-3 text-xs-right" v-if="item.availability !== '0'">
              {{ item.price | currency }}
            </div>
            <div class="col-xs-3 text-xs-center notAvailableColor" v-if="item.availability === '0'">
              N/A
            </div>
          </div>
          <hr v-if="idx != menuCategory.menuList.length - 1">
        </div>
      </div>

    <!-- </transition-group> -->

	</div>
</template>


<script>
import { mapGetters } from "vuex";

import { NEW_CART, } from "@/store/mutations.type";
import { ADD_TO_CART, SET_PERSISTENT_EXPANDED_MENU_CATEGORY, SET_TEMP_ITEM } from "@/store/mutations.type";

export default {
  name: "MenuCategory",
  props: {
    menuCategory: {
      type: Object,
      required: true
    },
  },
  data () {
    return {
      
    }
  },
  computed: {
    ...mapGetters(["currentCart", "merchant"])
	},
  methods: {
    
    toggleCategory () {
      
      this.$store.commit(SET_PERSISTENT_EXPANDED_MENU_CATEGORY, this.menuCategory.name);
    },

    checkItem (itemId) {
      
      if(!this.$store.state.merchant.merchant.currentlyOp){
        
        let toast = this.$toasted.show(this.$store.state.merchant.merchant.name + ' @ ' + this.$store.state.merchant.merchant.address + ' is closed now', { 
          theme: "outline", 
          position: "bottom-center", 
          duration : 2000,
        });

        return
      }
      
      let item = this.menuCategory.menuList[itemId];

      if(item.availability === "0"){
        
        let toast = this.$toasted.show('<b>'+item.name+'</b>' + ' is not available', { 
          theme: "outline", 
          position: "bottom-center", 
          duration : 2000,
        });

        // console.log("Item not available", item)
        return;
      }else{
        // console.log("Item available", item)
      }

      item.customisationApplied = [ ]
      
      if(item.customisation.length >= 1){
        // Has customisation
      
        this.$store.commit(SET_TEMP_ITEM, item);

        this.$router.push({ name: 'itemCustomisation', params: { merchantId: this.currentCart.merchant.friendlyId, itemId: item.id }})
        
      }else{
        // Doesnt have customisation

        let toast = this.$toasted.show('<b>' + item.name.toUpperCase() + ' </b>&nbsp;added to your cart', { 
          theme: "bubble", 
          position: "bottom-center", 
          duration : 2000,
          
          action : {
              text : 'Cart',
              onClick : (e, toastObject) => {
                  toastObject.goAway(0);
                  this.$router.push({ name: 'cart', params: { }})
              }
          },
        });
      
        this.$store.commit(ADD_TO_CART, item);
      }

    }
  }
};
</script>

<style scoped>
  .mainColor{
    color: #ffba00;
    font-weight: bold;
  }
  .menuCategory{
    padding: 12px 0;
  }
  .menuCategory>h4{
    padding-left: 12px;
  }
  .menuList{
    margin-top: -10px;
    padding-top: 12px;
    background-color: rgb(248, 248, 248);
    border-radius: 15px;
    /* max-height: 100px; */
    transition: all 5s;
  }
  .item{
    padding: 10px;
  }
  .itemContent{
    margin-top: -10px;
    padding-bottom: 12px;
  }
  .itemName{
    margin-left: -18px;
    margin-top:-5px;
  }
  /* .img-fluid{
    border-radius: 10px;
  } */
  .img-fluid{
    object-fit: cover;
    border-radius: 8px;
    width: 100%;
  }
  hr{
    margin: 0;
  }
  
  .itemImage{
    position: relative;
  }

  .notAvailableColor{
    color: #aaa;
  }

  .notOpOverlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0.7;
    background-color: #777;
    border-radius: 8px;
  }

  .notOpOverlay > .text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    font-weight: bold;
    color: black;
  }
</style>


