import { ADD_TO_CART, REMOVE_FROM_CART, 
         NEW_CART, 
         SET_TEMP_ITEM, REMOVE_TEMP_ITEM, 
         SET_COLLECTION_METHOD,
         SET_VOUCHER,
         ADD_CUSTOM_TO_TEMP_ITEM, REMOVE_CUSTOM_FROM_TEMP_ITEM } from "@/store/mutations.type";

const state = {
  errors: null,
  currentCart: {
    merchant: {
      id: '',
      name: '',
      address: '',
      friendlyId: '',
      image: '',
      currentDiscount: 0,
      availableCollectionMethod: 0,
      availableCollectionMethod: 0,
    },
    collection: {
      method: '-1',
      timing: '-1',
      tableNumber: 0,
    },
    items: [ ],
    notes: '',
    promotion: {
      id: '',
      code: '',
      name: '',
      type: '',
      value: '',
    },
    tempItem: { },
    subTotalValue: 0,
    discountValue: 0,
    taxValue: 0,
    totalValue: 0,
  }
};

const getters = {
  currentCart(state) {
    return state.currentCart;
  },

  tempItem(state) {
    return state.currentCart.tempItem;
  },
  
  calcSubTotalStore(state) {
    let subTotal = 0
    console.log('currentCart', state.currentCart, state.currentCart.items.length, 'items')
    for(let i = 0; i < state.currentCart.items.length; i++){
      console.log(subTotal, state.currentCart.items[i].cartValue)
      subTotal += parseFloat(state.currentCart.items[i].cartValue)
    }
    
    state.currentCart.subTotalValue = subTotal;

    return subTotal;
  },

  calcDiscountStore(state) {

      /**
       * prerequisite_subtotal_range
       * value.type : BOGO / PERCENTAGE / FIXED_AMOUNT
       * value.allocation : EACH / ACROSS
       * value.amount : numeric
       *  
       */

      let voucher = state.currentCart.promotion
      let discValue = 0;

      if(!voucher){

        console.log("!!!! No voucher found")
        
        let subTotal = 0;

        for(let i = 0; i < state.currentCart.items.length; i++){
          subTotal += parseFloat(state.currentCart.items[i].cartValue)
        }
        
        state.currentCart.discountValue = subTotal * (state.currentCart.merchant.currentDiscount / 100);

        return state.currentCart.merchant.currentDiscount
      }
      
      console.log("calcDiscount", voucher, "items in cart", state.currentCart.items.length)
      
      if(voucher.prerequisite_subtotal_range > 0){
        if(state.currentCart.subTotalValue < voucher.prerequisite_subtotal_range){
          throw "NOT_ENOUGH_MIN"
        }
      }

      if(voucher.value.amount <= 0 && voucher.value.type !== "BOGO"){
        throw "INVALID_VOUCHER"
      }

      if(voucher.value.type === "BOGO" && state.currentCart.items.length <= 1){
        throw "NOT_ENOUGH_QTY"
      }
      
      if(voucher.value.type === "BOGO"){
        console.log("BOGO hit")

        discValue = 999;
        
        for(let i = 0; i < state.currentCart.items.length; i++){
          discValue = Math.min(discValue, parseFloat(state.currentCart.items[i].cartValue))
        }
        
      }else if(voucher.value.type === "PERCENTAGE"){
        // Calculate
        console.log("PERCENTAGE called", voucher.value.allocation)

        if(parseFloat(voucher.value.amount) <= 0 || parseFloat(voucher.value.amount) > 1){
          throw "INVALID_VOUCHER"
        }

        if(voucher.value.allocation === "EACH"){
          console.log("PERCENTAGE, EACH")

          discValue = 0
        
          for(let i = 0; i < state.currentCart.items.length; i++){
            discValue += voucher.value.amount * parseFloat(state.currentCart.items[i].cartValue)
          }
          
        }else if(voucher.value.allocation === "ACROSS"){
          console.log("PERCENTAGE, ACROSS")

          discValue = voucher.value.amount * state.currentCart.subTotalValue
          
        }else{
          throw "INVALID_VOUCHER"
        }
        
        state.currentCart.promotion.cartValue = discValue;
        state.currentCart.discountValue = discValue;

      
      }else if(voucher.value.type === "FIXED_AMOUNT"){
        // Calculate
        console.log("FIXED_AMOUNT called", voucher.value.allocation)

        if(voucher.value.allocation === "EACH"){
          console.log("FIXED_AMOUNT, EACH", parseFloat(voucher.value.amount), state.currentCart.items.length,  parseFloat(voucher.value.amount) * state.currentCart.items.length)

          discValue = parseFloat(voucher.value.amount) * state.currentCart.items.length
          
        }else if(voucher.value.allocation === "ACROSS"){
          console.log("FIXED_AMOUNT, ACROSS")

          discValue = voucher.value.amount
          
        }else{
          state.currentCart.promotion = false
          throw "INVALID_VOUCHER"
        }


      }else{
        state.currentCart.promotion = false
        throw "INVALID_VOUCHER"
      }
      
      discValue = Math.floor(Math.min(discValue, state.currentCart.subTotalValue) * 100) / 100

      console.log("discountValue", discValue)
      
      state.currentCart.promotion = voucher;
        
      state.currentCart.promotion.cartValue = discValue;
      state.currentCart.discountValue = discValue;

      return discValue;
  },

  calcTaxStore(state) {
    state.currentCart.taxValue = 0;

    let taxValue = 0;

    console.log("calcTaxStore", state.currentCart.subTotalValue, parseFloat(state.currentCart.merchant.taxPercent))

    if(state.currentCart.merchant.taxPercent>0) {
      // taxValue = 0
      taxValue = (state.currentCart.subTotalValue * ( parseFloat(state.currentCart.merchant.taxPercent) / 100 ));
    }else{
      taxValue = 0
    }

    state.currentCart.taxValue = taxValue;
    
    return state.currentCart.taxValue;
  },

  calcTotalStore(state) {
    state.currentCart.totalValue = state.currentCart.subTotalValue - state.currentCart.discountValue + state.currentCart.taxValue;
    return state.currentCart.totalValue;
  },
};
 
const actions = { };

const mutations = {
  
  [SET_TEMP_ITEM](state, item) {
    // console.log('SET_TEMP_ITEM - START -', state.currentCart)
    // console.log('SET_TEMP_ITEM - ADDING -', item)
    state.currentCart.tempItem = item
    // console.log('temp item', state.currentCart.tempItem)
  },
  
  [REMOVE_TEMP_ITEM](state) {
    state.currentCart.tempItem = { }
    // console.log(state.currentCart.tempItem)
  },
  
  [ADD_CUSTOM_TO_TEMP_ITEM](state, customItem) {
    state.currentCart.tempItem.customisationApplied.push( customItem )
    // console.log("Custom added")
    // console.log(state.currentCart.tempItem)
  },
  
  [REMOVE_CUSTOM_FROM_TEMP_ITEM](state, customItem) {
    // Search location

    let idx = 0;
    
    // console.log('this', state.currentCart.tempItem.customisationApplied.length)

    for (let i = 0; i < state.currentCart.tempItem.customisationApplied.length; i++) { 
      // console.log(state.currentCart.tempItem.customisationApplied[i], customItem, state.currentCart.tempItem.customisationApplied[i] == customItem)
      if(state.currentCart.tempItem.customisationApplied[i] === customItem){
        // console.log('Found similar at', i)
        state.currentCart.tempItem.customisationApplied.splice(i, 1);
      }
    }
    
    // console.log('that', state.currentCart.tempItem.customisationApplied.length)

    // console.log("Custom removed")
  },
  
  [ADD_TO_CART](state, item = false) {
    console.log('ADD_TO_CART - START -', state.currentCart, item)

    let _item = false;
    
    if(item){
      console.log('ADD_TO_CART - ADDING - item param supplied', item)
      _item = item;
    }else{
      console.log('ADD_TO_CART - ADDING - item param not supplied, adding tempItem', state.currentCart.tempItem)
      _item = JSON.parse(JSON.stringify(state.currentCart.tempItem));
      state.currentCart.tempItem = { }
    }

    _item.cartValue = 100;
    
    let cartValue = parseFloat(_item.price)
    
    for(let i = 0; i < _item.customisationApplied.length; i++){
      cartValue += parseFloat(_item.customisationApplied[i].price)
    }

    _item.cartValue = cartValue;

    state.currentCart.items.push( _item )
    

    try{
      getters.calcSubTotalStore(state) 
      getters.calcDiscountStore(state);
      getters.calcTaxStore(state);
    }catch(err){
      console.log("error found", err)
      state.currentCart.promotion = false;
      throw err
    }
    
    console.log('ADD_TO_CART - END -', state.currentCart)
  
  },

  [SET_VOUCHER](state, voucher) {
    if(voucher !== null){
      console.log('Voucher applied', voucher)

      state.currentCart.promotion = voucher;

      try{
        getters.calcSubTotalStore(state)
        getters.calcDiscountStore(state);
        getters.calcTaxStore(state);
      }catch(err){
        console.log("error found", err)
        state.currentCart.promotion = false;
        throw err
      }
      
    }else{
      // Reset voucher
      state.currentCart.promotion = false;
      state.currentCart.discountValue = 0;
    }

    console.log('Current cart after voucher', state.currentCart)
  },

  [SET_COLLECTION_METHOD](state, collectionMethodObj) {
    console.log('Collection method changed')
  },
  
  [REMOVE_FROM_CART](state, idx) {
    console.log('this', state.currentCart.items)
    state.currentCart.items.splice(idx, 1);
    console.log('that', state.currentCart.items)
    state.errors = {};
    
    try{
      getters.calcDiscountStore(state);
    }catch(err){
      console.log("error found", err)
      state.currentCart.promotion = false;
      state.currentCart.discountValue = 0;
      throw err
    }
  },

  [NEW_CART](state, merchant = false) {
    state.currentCart = {
      items: [ ],
      notes: '',
      tempItem: { },
      promotion: false,
      collection: {
        method: '-1',
        timing: '-1',
        tableNumber: 0,
      },
      subTotalValue: 0,
      discountValue: 0,
      taxValue: 0,
      totalValue: 0,
    }

    if(merchant == false){
      state.currentCart.merchant = state.currentCart.merchant;
    }else{
      state.currentCart.merchant = merchant;
    }

    console.log('NEW currentCart', state.currentCart)
  },
  

};

export default {
  state,
  actions,
  mutations,
  getters
};


