import ApiService from "@/common/api.service";
import JwtService from "@/common/jwt.service";

import {
  LOGIN,
  LOGOUT,
  REGISTER,
  CHECK_AUTH,
  UPDATE_USER,
  UPDATE_PASSWORD,
  FORGET_PASSWORD_REQUEST 
} from "@/store/actions.type";

import { SET_AUTH, PURGE_AUTH, SET_ERROR } from "@/store/mutations.type";

const state = {
  errors: null,
  user: {
    id: '',
    username: '',
    email: '',
    wallet: {
      ccy: '',
      value: 0,
    },
    token: '',
    lastTransaction: [ ]
  },
  isAuthenticated: !!JwtService.getToken()
};

const getters = {
  currentUser(state) {
    console.log('currentUser()', state.user)
    return state.user;
  },
  isAuthenticated(state) {
    // console.log('isAuthenticated()', JwtService.getToken(), !!JwtService.getToken(), state.isAuthenticated)
    return state.isAuthenticated;
  }
};

const actions = {
  [LOGIN](context, credentials) {

    ApiService.setHeader(false);
    
    console.log('Logging user in')
    return new Promise(resolve => {
      ApiService.post("users/login", { email: credentials.email, password: credentials.password })
        .then(({ data }) => {
          console.log('login return', data)
          context.commit(SET_AUTH, {
            user: data.user,
            fullLoad: true,
          });
          resolve(data);
        }).catch((error) => {
          console.log('LOGIN', error)
          resolve(false)
        });
    });
  },

  [UPDATE_PASSWORD](context, credentials) {

    ApiService.setHeader(true);
    
    console.log('Logging user in')
    
    return ApiService.post("users/update/password", { 
        data_update: 'password', 
        old_password: credentials.password_old,
        p1: credentials.password_1,
        p2: credentials.password_2 
      });
  },

  [FORGET_PASSWORD_REQUEST](context, email) {

    ApiService.setHeader(false);

    return ApiService.post("users/forgetpassword", { email: email });
  },

  [LOGOUT](context) {

    ApiService.setHeader();

    new Promise(resolve => {
      ApiService.query("users/logout", { })
        .then(({ data }) => {
          context.commit(PURGE_AUTH);
          resolve(data);
        });
    });

  },

  [REGISTER](context, credentials) {

    ApiService.setHeader(false);

    return ApiService.post("users", credentials);
        // .then(({ data }) => {
        //   // context.commit(SET_AUTH, data.user);
        //   resolve(true);
        // })
        // .catch(({ response }) => {
        //   console.log(response)
        //   context.commit(SET_ERROR, response.errors);
        //   reject(response);
        // });
  },

  [CHECK_AUTH](context, withTransactions = false) {
    let additionalParam = { }

    console.log('Checking auth withTransactions', withTransactions, state.user)

    if(withTransactions){
      additionalParam = { withTransactions: 1 }
    }

    // if(!state.user){
    //   throw "not logged in"
    // }
    
    if (JwtService.getToken()) {
      
      ApiService.setHeader();

      new Promise(resolve => { ApiService.query("users/checktoken", { params: additionalParam })
        .then(({ data }) => {
          
          if(data === undefined){
            resolve(false)
          }

          if(withTransactions){
            // withTransactions = Full load
            // context.commit(SET_AUTH, data, true);   
            context.commit(SET_AUTH, {
              user: data,
              fullLoad: true,
            });
          }else{
            // NOT withTransactions = checkToken only
            // context.commit(SET_AUTH, data); 
            context.commit(SET_AUTH, {
              user: data,
              fullLoad: false,
            });
          }
          
          resolve(true)
        })
        .catch(function (error) {
          
          context.commit(PURGE_AUTH);
          
          throw error
        });
      })
      
    } else {
      console.log('Active token not found')

      return false
    }
  },

  [UPDATE_USER](context, payload) {
    const { email, username, } = payload;
    const user = {
      email,
      username,
    };
    if (password) {
      user.password = password;
    }

    return ApiService.put("user", user).then(({ data }) => {
      // context.commit(SET_AUTH, data.user, true);
      context.commit(SET_AUTH, {
        user: data.user,
        fullLoad: true,
      });
      return data;
    });
  }
};

const mutations = {
  [SET_ERROR](state, error) {
    state.errors = error;
  },

  [SET_AUTH](state, info) {
    // console.log(user)
    // console.log('Set auth', user)

    console.log('SET_AUTH', info)

    state.isAuthenticated = true;

    if(info.fullLoad){
      console.log('FullLoad', info.user)
      state.user = info.user;
    }else{
      console.log('Update user not full load', info.user)
      state.user.wallet = info.user.wallet;
      state.user.token = info.user.token;
    }

    JwtService.saveToken(state.user.token);

    state.errors = {};
  },

  [PURGE_AUTH](state) {
    console.log('Purging auth info')
    state.isAuthenticated = false;
    state.user = false;
    state.errors = {};
    JwtService.destroyToken();
    ApiService.setHeader(false);
  }
};

export default {
  state,
  actions,
  mutations,
  getters
};


