<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>Settings</h1>
        <hr>
      </div>
    </div>
    <div class="row" v-if="hasActiveUser()">
      <div class="col-xs-12">
        <router-link :to="{ name: 'profileUpdate' }">
          <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">person</i> Profile Update</h3>
        </router-link>
        <hr>
      </div>
    </div>
    <div class="row" v-if="!hasActiveUser()">
      <div class="col-xs-12">
        <router-link :to="{ name: 'login' }">
          <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">person</i> Login</h3>
        </router-link>
        <hr>
      </div>
    </div>
    <div class="row" v-if="!hasActiveUser()">
      <div class="col-xs-12">
        <router-link :to="{ name: 'register' }">
          <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">how_to_reg</i> Register</h3>
        </router-link>
        <hr>
      </div>
    </div>
    <div class="row" v-if="hasActiveUser()">
      <div class="col-xs-12">
        <router-link :to="{ name: 'passwordUpdate' }" >
          <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">security</i> Password Update</h3>
        </router-link>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">contact_support</i> FAQ</h3>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <a href="mailto:cs@stash.asia">
          <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">question_answer</i> Contact</h3>
        </a>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">description</i> Privacy Policy</h3>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">description</i> Terms of Service</h3>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">description</i> Legal</h3>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">description</i> Security</h3>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h3>&nbsp;&nbsp;<i class="md-36 material-icons main">business_center</i> Business contact</h3>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
				<br>
				<button @click="updateApp" class="btn btn-outline-success btn-block btn-lg">
					App update
				</button>
      </div>
    </div>
    <div class="row" v-if="hasActiveUser()">
      <div class="col-xs-12">
				<br>
				<br>
				<button @click="logout" class="btn btn-outline-danger btn-block">
					Logout
				</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { LOGOUT } from "@/store/actions.type";

import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";
  
export default {
  name: 'viewSettings',

  computed: {
    ...mapGetters(['currentUser'])
  },

  methods: {

    hasActiveUser() {
      if(this.currentUser){
        if(this.currentUser.id === ""){
          return false
        }else{
          return true
        }
      }
      
      return false;
    },

    logout () {
          
      this.$swal({
        title: 'Logout STASH',
        type: 'error',
        confirmButtonText: 'Logout',
        showCancelButton: true,
        confirmButtonText: 'Keep',
        cancelButtonText: 'Logout',
        showCloseButton: true,
        showLoaderOnConfirm: true
      }).then((result) => {
        if(result.value) {
      
          this.$ga.event('account', 'logout', 'logout', 0);

          return 0;

        } else {
          // this.$swal('Cancelled', 'Your file is still intact', 'info')
      
          this.$ga.event('account', 'logout', 'logout', 1);

          this.$store.dispatch(LOGOUT).then(() => {
            this.$router.push({ name: "home" });
          });
          
        }
      })

    },
      
    updateApp () {
      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });
      if(window.navigator && navigator.serviceWorker) {
        navigator.serviceWorker.getRegistrations()
        .then(function(registrations) {
          for(let registration of registrations) {
            registration.unregister();
          }
        });
      }
      window.location.reload(true) 
      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });
      this.$swal.fire({
        type: 'success',
        text: 'Your STASH is up to date',
        showConfirmButton: false,
        showCloseButton: true,
      })
    }
  }
}
</script>

<style>
h3{
  margin: 15px 0 15px 0;
}
</style>



