<template>
  <div class="auth-page">
    <div class="container page">
      <div class="row">
        <div class="col-xs-12">
          <h1 class="text-xs-left">Sign up</h1>
          <p class="text-xs-left">
            <router-link :to="{ name: 'login' }">
              Have an account?
            </router-link>
          </p>
          <ul v-if="errors" class="error-messages">
            <li v-for="(v, k) in errors" :key="k">{{ k }} {{ v | error }}</li>
          </ul>
          <form v-on:submit.prevent="onSubmit">
            <fieldset class="form-group">
              <input
                class="form-control form-control-lg"
                type="text"
                v-model="username"
                placeholder="Username"
              />
            </fieldset>
            <fieldset class="form-group">
              <input
                class="form-control form-control-lg"
                type="text"
                v-model="email"
                placeholder="Email"
              />
            </fieldset>
            <fieldset class="form-group">
              <input
                class="form-control form-control-lg"
                type="password"
                v-model="password"
                placeholder="Password"
              />
            </fieldset>
            <fieldset class="form-group form-inline">
              <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="gender" id="genderMale" v-model="gender" value="1">
                  <label class="form-check-label" for="genderMale">
                    <h3>&nbsp;Male</h3>
                </label>
                &nbsp;
                &nbsp;
                &nbsp;
                &nbsp;
                &nbsp;
                &nbsp;
                &nbsp;
                &nbsp;
                <input class="form-check-input" type="radio" name="gender" id="genderFemale" v-model="gender" value="0">
                <label class="form-check-label" for="genderFemale">
                    <h3>&nbsp;Female</h3>
                </label>
              </div>
            </fieldset>
            <button class="btn btn-lg btn-primary pull-xs-right">
              Sign up
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { REGISTER } from "@/store/actions.type";
import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

export default {
  name: "viewRegister",
  data() {
    return {
      username: "",
      email: "",
      password: "",
      gender: "",
    };
  },
  computed: {
    ...mapState({
      errors: state => state.auth.errors
    })
  },
  methods: {
    onSubmit() {
      this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });

      this.$store.dispatch(REGISTER, {
          email: this.email,
          password: this.password,
          name: this.username,
          gender: this.gender
        })
        .then(() => {
          this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });
          // alert('')
					
          this.$swal.fire({
            type: 'success',
            text: 'Registration success, please login to your account now',
            showConfirmButton: false,
            showCloseButton: true,
          })

          this.$router.push({ name: "login" })
        })
        .catch(error => {
          this.$store.commit(SWITCH_OVERLAY_LOADER, { root: true });
          
          console.log('alert error', error, '|', error.response.status)

          if(error.response.status === 409){

            this.$ga.event('register', 'account_found', 'ask', true);
            
            this.$swal({
              title: 'Account found?',
              text: 'Login or forget your password?',
              type: 'warning',
              showCancelButton: true,
              confirmButtonText: 'Login',
              cancelButtonText: 'Forget Password',
              showLoaderOnConfirm: true
            }).then((result) => {
              if(result.value) {
                this.$ga.event('register', 'account_found', 'go_to_login', true);
                this.$router.push({ name: 'login', })
              } else {
                this.$ga.event('register', 'account_found', 'go_to_forget_password', true);
                this.$router.push({ name: 'forget_password', })
              }
            })

            return;

          }else{

            this.$swal.fire({
              type: 'warning',
              text: 'Unable to reach server, please check again in a few minutes.',
              showConfirmButton: false,
              showCloseButton: true,
            })
          }
          

        });
    }
  }
};
</script>



