import Vue from 'vue'
import Router from 'vue-router'

import store from '@/store'
import { CHECK_AUTH } from '@/store/actions.type'

import { SWITCH_OVERLAY_LOADER, PURGE_AUTH } from "@/store/mutations.type";

// import HelloWorld from '@/components/HelloWorld'

// import firebase from 'firebase'

// import Comics from '@/components/Comics'

import Login from '@/views/Login'
// import LoginFacebook from '@/views/LoginFacebook'
import Register from '@/views/Register'
import Home from '@/views/Home'
import ProfileUpdate from '@/views/ProfileUpdate'
import PasswordUpdate from '@/views/PasswordUpdate'
import Settings from '@/views/Settings'
import ForgetPasswordRequest from '@/views/ForgetPasswordRequest'
import Wallet from '@/views/Wallet'
import WalletTopUp from '@/views/WalletTopUp'
import Inbox from '@/views/Inbox'
import Merchant from '@/views/Merchant'
import MerchantDeal from '@/views/MerchantDeal'
import Vouchers from '@/views/Vouchers'
import ItemCustomisation from '@/views/ItemCustomisation'
import Cart from '@/views/Cart'
import OrderDetails from '@/views/OrderDetails'



Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      name: 'login',
      path: '/login',
      component: Login,
    },
    {
      name: 'register',
      path: '/register',
      component: Register,
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
    },
    {
      name: 'settings',
      path: '/settings',
      component: Settings,
      meta: {
        // requiresAuth: true
      }
    },
    // {
    //   name: 'profile',
    //   path: '/profile',
    //   component: ProfileUpdate,
    //   meta: {
    //     requiresAuth: true
    //   }
    // },
    {
      name: 'profileUpdate',
      path: '/profile/update/info',
      component: ProfileUpdate,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'passwordUpdate',
      path: '/profile/update/password',
      component: PasswordUpdate,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'merchant',
      path: '/merchants/:merchantId',
      component: Merchant,
      props: true,
    },
    {
      name: 'vouchers',
      path: '/merchants/:merchantId/vouchers',
      component: Vouchers,
      props: true,
    },
    {
      name: 'itemCustomisation',
      path: '/merchants/:merchantId/items/:itemId',
      component: ItemCustomisation,
      props: true,
    },
    {
      name: 'inbox',
      path: '/inbox',
      component: Inbox,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'wallet',
      path: '/wallet',
      component: Wallet,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'walletTopUp',
      path: '/wallet/topup',
      component: WalletTopUp,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'cart',
      path: '/cart',
      component: Cart,
      meta: {
        // requiresAuth: true
      }
    },
    {
      name: 'orderDetails',
      path: '/orders/:orderId',
      component: OrderDetails,
      meta: {
        requiresAuth: true
      }
    },
    {
      name: 'forget_password',
      path: '/forget_password',
      component: ForgetPasswordRequest,
    },

    // {
    //   path: '/comics',
    //   name: 'Comics',
    //   component: Comics,
    //   meta: {
    //     requiresAuth: true
    //   }
    // },

    // otherwise redirect to home
    { 
      name: 'NotFound',
      path: '*', 
      redirect: '/' 
    }
  ],
  
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
        return savedPosition;
    }
    if (to.hash) {
        return { selector: to.hash };
    }
    return { x: 0, y: 0 };
  }
})




router.beforeEach((to, from, next) => {

  let requiresAuth = to.matched.some(record => record.meta.requiresAuth)

  let isPublicOnly = [ 'login', 'register', 'forget_password' ].includes(to.name)
  
  store.commit(SWITCH_OVERLAY_LOADER, { root: true });

  let isLogin = store.dispatch(CHECK_AUTH);
  
  store.commit(SWITCH_OVERLAY_LOADER, { root: true });
  
  if (requiresAuth){
    
    isLogin.then(values => {
      
      if(values === false){
        next('login')
      }
    })
    .catch((error) => {
      console.log("PURGE_AUTH1")
      store.commit(PURGE_AUTH, { root: true });
      this.$swal.fire({
        type: 'info',
        text: 'You are logged out',
        showConfirmButton: false,
        showCloseButton: true,
      })
      console.log("router", error);
      next('login')
      return
    });
    
  }else{
    
    if(isPublicOnly){
      
      isLogin.then((values) => {

          if(values == false){
            // Not auth
            next()
            return
          }else{

            // Auth kick out
            next('home')
            return
          }
      })
      .catch((error) => {
        console.log("PURGE_AUTH2")
        store.commit(PURGE_AUTH, { root: true });
        this.$swal.fire({
          type: 'info',
          text: 'You are logged out',
          showConfirmButton: false,
          showCloseButton: true,
        })
        console.log("router", error);
        next('login')
        return
      });
    }
    
  }

  next()

});




export default router


